.App-Header {
  width: 50%;
}

.App-Icon-Menu {
  width: 50%;
  height: 60px;
  margin: auto 20px;
  text-align: right;
}

.mixBlend {
  background: rgba(137, 84, 162, 0.4);
}

.Info-Section-parent {
  height: 720px;
  background: url(./assets/images/business-solution.png);
}

.OurServices-Info-Section-parent {
  background: url(./assets/images/our-services.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

.Info-Section {
  height: inherit;
  background: rgba(137, 84, 162, 0.4);
  mix-blend-mode: color;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.Vision-Info-Section-Parent {
  height: 705px;
  background: url(./assets/images/vision.png);
}
.Vision-Info-Section {
  height: inherit;
  background: rgba(137, 84, 162, 0.4);
  mix-blend-mode: color;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

button,
button:focus {
  outline: none;
  background: transparent;
  border: 1px solid transparent;
}

button:active {
  outline: none;
  background: transparent;
  border: 1px solid transparent;
}

.container {
  position: absolute;
  top: 10%;
  left: 23%;
  width: 50%;
}

.text {
  display: inline;
  width: 100%;
}

.read-or-hide {
  color: rgb(192, 192, 192);
  cursor: pointer;
}

ul {
  margin: 0px;
}

.imageContainer {
  position: relative;
  width: 100%;
  height: 100%;
}

.image-size {
  width: 90%;
  height: 90%;
}

.gfg-img {
  display: block;
  width: 100%;
  height: 321px;
  border-radius: 10px;
}

.title {
  background-color: rgb(0, 0, 0, 0.5);
  position: absolute;
  height: 50%;
  color: white;
  width: 100%;
  font-size: 25px;
  text-align: center;
  opacity: 0;
  transition: 0.6s;
  bottom: 10px;
  border-bottom-left-radius: 10px;
  left: 10px;
  border-bottom-right-radius: 10px;
}

.imageContainer:hover .title {
  /*becomes visible on hover*/
  opacity: 1;
}

.container-image {
  position: relative;
}

.background-color {
  background: rgba(137, 84, 162, 0.4);
}

.bottom-left {
  position: absolute;
  bottom: 8px;
  left: 16px;
}

@media (min-width: 600px) {
  .top-left {
    position: absolute;
    top: 8px;
    left: 16px;
    width: 100%;
  }
}

.top-right {
  position: absolute;
  top: 8px;
  right: 16px;
}

.bottom-right {
  position: absolute;
  bottom: 8px;
  right: 16px;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.image-responsive {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  display: block;
}

.container-image2 {
  position: relative;
  padding: 10px;
}

@media (max-width: 599px) {
  .image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 250px;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}
@media (max-width: 899px) {
  .image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 250px;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}
@media (min-width: 900px) {
  .image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 321px;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}

@media (max-width: 599px) {
  .image-team {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}
@media (max-width: 899px) {
  .image-team {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}
@media (min-width: 900px) {
  .image-team {
    opacity: 1;
    display: block;
    width: 100%;
    height: 321px;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}

.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}

.container-image2:hover .image {
  opacity: 0.3;
}

.container-image2:hover .middle {
  opacity: 1;
}

.text {
  color: black;
  font-size: 16px;
  padding: 10px;
}

.header-services {
  font-weight: 500;
  font-size: 40px;
  line-height: 160.06%;
  /* or 64px */

  text-align: center;
  letter-spacing: 0.005em;

  color: #ffffff;
  margin: 0;
}

.candidate-name {
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  color: rgb(22, 22, 22);
  padding: 20px 0px 10px 20px;
}

.candidate-designation {
  font-weight: 500;
  font-size: 16px;
  color: rgb(22, 22, 22);
  padding: 0px 0px 10px 20px;
}

.candidate-description {
  font-weight: 300;
  font-size: 16px;
  color: rgb(22, 22, 22);
  padding: 0px 0px 10px 20px;
}
ul li {
  list-style-type: disc;
}

.success-block {
  text-align: center;
  width: 100%;
  min-height: 200px;
  height: auto;
  padding-bottom: 20px;
}

.success-text {
  max-width: 500px;
  margin: auto;
  height: 100%;
  border: 2px solid green;
  border-radius: 10px;
  display: inline-flex;
  align-items: center;
}

.success-para {
  margin: 20px;
}

.success-icon {
  width: 35px !important;
  height: 35px !important;
  color: green;
}

.bg-btn {
  background-color: rgb(137, 85, 157) !important;
  font-weight: 700 !important;
  height: 50px !important;
}

@media (max-width: 599px) {
  .bg-btn {
    font-size: 22px !important;
  }
}
@media (max-width: 899px) {
  .bg-btn {
    font-size: 22px !important;
  }
}
@media (min-width: 900px) {
  .bg-btn {
    font-size: 18px !important;
  }
}

.dialog-text {
  padding: 0 20px 20px 20px;
}

.dialog-text p,
h3 {
  font-size: 10px;
}

.image-dimension {
  max-width: 499px;
  max-height: 712px;
}

ol {
  margin: 0;
}
